@font-face {
  font-family: 'ecolex-icons';
  src: url('../fonts/ecolex-icons.eot');
  src: url('../fonts/ecolex-icons.eot?#iefix') format('embedded-opentype'), url('../fonts/ecolex-icons.woff') format('woff'), url('../fonts/ecolex-icons.ttf') format('truetype'), url('../fonts/ecolex-icons.svg#ecolex-icons') format('svg');
}
#sliding-popup {
  font-size: 14px;
  text-align: center;
}
#sliding-popup .list-inline > li {
  margin-top: 11px;
  vertical-align: middle;
}
#sliding-popup .find-more-button {
  color: #fff;
}
.jumbotron {
  margin-bottom: 45px;
  padding-bottom: 30px;
  padding-top: 30px;
}
.jumbotron h1 {
  margin-top: 0;
}
.jumbotron .text-primary {
  color: #0095f3;
}
#search-form .form-control {
  border-radius: 5px;
  font-size: 16px;
  padding-right: 120px;
}
#search-form .has-feedback .form-control {
  padding-left: 45px;
}
#search-form .form-control-feedback {
  color: #666;
  left: 0;
  right: auto;
}
#search-form .btn {
  line-height: 27px;
  position: absolute;
  right: 4px;
  top: 4px;
  width: 100px;
}
#search-form .btn-primary {
  background-color: #0077c0;
  border-color: #0077c0;
  color: #fff;
}
#search-form .btn-primary:focus,
#search-form .btn-primary.focus {
  background-color: #00578d;
  border-color: #002841;
  color: #fff;
}
#search-form .btn-primary:hover {
  background-color: #00578d;
  border-color: #005183;
  color: #fff;
}
#search-form .btn-primary:active,
#search-form .btn-primary.active,
.open > .dropdown-toggle#search-form .btn-primary {
  background-color: #00578d;
  border-color: #005183;
  color: #fff;
}
#search-form .btn-primary:active:hover,
#search-form .btn-primary.active:hover,
.open > .dropdown-toggle#search-form .btn-primary:hover,
#search-form .btn-primary:active:focus,
#search-form .btn-primary.active:focus,
.open > .dropdown-toggle#search-form .btn-primary:focus,
#search-form .btn-primary:active.focus,
#search-form .btn-primary.active.focus,
.open > .dropdown-toggle#search-form .btn-primary.focus {
  background-color: #004169;
  border-color: #002841;
  color: #fff;
}
#search-form .btn-primary:active,
#search-form .btn-primary.active,
.open > .dropdown-toggle#search-form .btn-primary {
  background-image: none;
}
#search-form .btn-primary.disabled:hover,
#search-form .btn-primary[disabled]:hover,
fieldset[disabled] #search-form .btn-primary:hover,
#search-form .btn-primary.disabled:focus,
#search-form .btn-primary[disabled]:focus,
fieldset[disabled] #search-form .btn-primary:focus,
#search-form .btn-primary.disabled.focus,
#search-form .btn-primary[disabled].focus,
fieldset[disabled] #search-form .btn-primary.focus {
  background-color: #0077c0;
  border-color: #0077c0;
}
#search-form .btn-primary .badge {
  background-color: #fff;
  color: #0077c0;
}
.panel {
  border-width: 2px;
}
.panel-tour .tour-title {
  margin-top: 22px;
}
.panel-resources .list-resources {
  margin-bottom: 0;
}
.panel-resources .list-resources > dd {
  margin-bottom: 11px;
}
@media (max-width: 991px) {
  .panel-tour:last-child {
    margin-bottom: 22px;
  }
}
@media (min-width: 992px) {
  .row-flex,
  .col-flex {
    display: flex;
  }
  .panel-tour {
    align-items: center;
    display: flex;
  }
}
.field--label {
  color: #212121;
  font-size: 14px;
  padding-top: 3px;
}
.page-node-type-court-decision .field {
  margin-bottom: 11px;
}
@media (min-width: 992px) {
  .page-node-type-court-decision .field--label {
    text-align: right;
  }
}
.footer {
  background-color: #eee;
  border-color: #ddd;
  color: #808080;
  font-size: 14px;
}
.footer a {
  color: #808080;
}
.footer a:hover,
.footer a:focus {
  color: #666;
}
#block-iucn-frontend-footer {
  float: right;
}
#block-iucn-frontend-footer .nav > li > a:hover,
#block-iucn-frontend-footer .nav > li > a:focus {
  text-decoration: underline;
}
